"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"

interface StaffMember {
  role: string
  name: string
}

const staffMembers: StaffMember[] = [
  { role: "Guru Besar", name: "Puan Aida Laila Aswati" },
  { role: "Penolong Kanan", name: "Puan Nur Ainun Shahadah" },
  { role: "Guru Data", name: "Puan Rosamanina" },
  { role: "Guru PA Sistem", name: "Puan Rosamanina" },
  { role: "Guru Kokurikulum", name: "Encik Muhamad Mulhim" },
  { role: "Setiausaha Kurikulum", name: "Puan Nur Dalila" },
  { role: "Guru Kebersihan & Keceriaan", name: "Puan Nur Asmalina" },
  { role: "Guru Disiplin", name: "Encik Wan Muhammed Sodiqin" },
  { role: "Guru Kantin", name: "Encik Amirul Hishamuddin" },
  { role: "Guru Bilik Buku & Stor", name: "Encik Muhammad Shahirul Hamzi" }
]

export default function OrganizationChart() {
  const [selectedStaff, setSelectedStaff] = useState<StaffMember | null>(null)

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
      {staffMembers.map((staff, index) => (
        <Card 
          key={index}
          className="cursor-pointer transition-all hover:shadow-lg hover:scale-105"
          onClick={() => setSelectedStaff(staff)}
        >
          <CardHeader className="p-4">
            <CardTitle className="text-lg font-semibold text-[#FFA500]">
              {staff.role}
            </CardTitle>
          </CardHeader>
          <CardContent className="p-4 pt-0">
            <p className="text-gray-600">{staff.name}</p>
          </CardContent>
        </Card>
      ))}

      {/* Modal for showing staff details */}
      {selectedStaff && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4" onClick={() => setSelectedStaff(null)}>
          <Card className="w-full max-w-md" onClick={e => e.stopPropagation()}>
            <CardHeader>
              <CardTitle className="text-xl text-[#FFA500]">{selectedStaff.role}</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-lg font-medium">{selectedStaff.name}</p>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  )
}
