"use client"

import { useState } from "react"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"

interface LogoUploaderProps {
  logo: string | null
  setLogo: (logo: string | null) => void
}

export default function LogoUploader({ logo, setLogo }: LogoUploaderProps) {
  const [error, setError] = useState<string>("")

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    setError("")

    if (!file) return

    // Validate file type
    if (!file.type.startsWith("image/")) {
      setError("Sila muat naik fail imej sahaja")
      return
    }

    // Validate file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      setError("Saiz fail mestilah kurang daripada 5MB")
      return
    }

    const reader = new FileReader()
    reader.onload = (event) => {
      setLogo(event.target?.result as string)
    }
    reader.readAsDataURL(file)
  }

  return (
    <div className="flex flex-col items-center gap-6">
      {/* Logo Preview */}
      {logo ? (
        <div className="relative w-48 h-48 rounded-lg overflow-hidden border-2 border-[#FFA500] shadow-lg">
          <Image
            src={logo}
            alt="Logo Sekolah"
            fill
            className="object-contain"
            priority
          />
        </div>
      ) : (
        <div className="w-48 h-48 rounded-lg border-2 border-dashed border-[#FFA500] flex items-center justify-center bg-[#FFA500]/5">
          <p className="text-[#FFA500] text-sm text-center px-4">
            Tiada logo dipilih.<br />
            Sila muat naik logo sekolah.
          </p>
        </div>
      )}

      {/* Upload Controls */}
      <div className="w-full max-w-xs space-y-4">
        <Input
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          className="cursor-pointer bg-white file:mr-4 file:py-2 file:px-4 file:rounded-md file:border-0 file:text-sm file:font-medium file:bg-[#FFA500] file:text-white hover:file:bg-[#FFA500]/90"
        />
        {error && (
          <p className="text-red-500 text-sm text-center">{error}</p>
        )}
        {logo && (
          <Button
            variant="destructive"
            onClick={() => setLogo(null)}
            className="w-full"
          >
            Buang Logo
          </Button>
        )}
      </div>
    </div>
  )
}
